.add-bot-base {
	display: flex;
	flex-direction: column;
	width: 200px;
	height: 200px;
	margin-top: 100px;
	background-color: white;
	border-radius: 10px;
	border: 1px solid whitesmoke;
	
	h1, p {
		margin: 0;
	}

	.img-container {
		height: 60%;
		
		img {
			max-width: 100%;
			max-height: 100%;
		}
	}

	.button-container {
		button {
			width: 50%;
		}
	}


}